import base from "./base";
import axios from "./axios";

const common = {
  //获取宝可梦列表
  getPokemonList(data) {
    return axios.get(base.api + "pokemon/", {
      params: data
    });
  },
  //获取宝可梦详细信息
  getPokemonInfo(data) {
    return axios.get(base.api + "pokemon/" + data.name);
  },
  //获取特性列表
  getAbilityList(data) {
    return axios.get(base.api + "ability/", { params: data });
  }
};

export default common;
