/**
 * http配置
 */
import axios from "axios";
import router from "../router";
import { message } from "ant-design-vue";
import qs from "qs";

let pending = []; //声明一个数组用于存储每个ajax请求的取消函数和ajax标识
let cancelToken = axios.CancelToken;

var instance = axios.create({
  timeout: 1000 * 30
});

instance.defaults.headers.post["Content-Type"] = "application/json";
instance.interceptors.request.use(
  config => {
    config.cancelToken = new cancelToken(c => {
      // 这里的ajax标识我是用请求地址&请求方式拼接的字符串，当然你可以选择其他的一些方式
      for (let p in pending) {
        //对比请求的参数
        let requstPrams =
          config.method == "get"
            ? JSON.stringify(config.params)
            : JSON.stringify(config.data);
        //当请求形式为get时要考虑请求的参数
        if (config.method == "get") {
          if (
            pending[p].u === config.url + "&" + config.method &&
            pending.requstPrams === requstPrams
          ) {
            //当当前请求在数组中存在时执行函数体
            c();
            pending.splice(p, 1); //把这条记录从数组中移除
          }
        } else {
          if (pending[p].u === config.url + "&" + config.method) {
            c();
            pending.splice(p, 1); //把这条记录从数组中移除
          }
        }
      }
    });
    let requstPrams =
      config.method == "get"
        ? JSON.stringify(config.params)
        : JSON.stringify(config.data);
    pending.push({ u: config.url + "&" + config.method, requstPrams });
    if (JSON.parse(sessionStorage.getItem("userinfo"))) {
      let token = JSON.parse(sessionStorage.getItem("userinfo")).token;
      config.headers["token"] = token;
    }
    if (config.method === "get") {
      config.paramsSerializer = function(params) {
        return qs.stringify(params, { arrayFormat: "repeat" });
      };
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);
instance.interceptors.response.use(
  response => {
    response.config.cancelToken = new cancelToken(c => {
      // 这里的ajax标识我是用请求地址&请求方式拼接的字符串，当然你可以选择其他的一些方式
      for (let p in pending) {
        //对比请求的参数
        let requstPrams =
          response.config.method == "get"
            ? JSON.stringify(response.config.params)
            : JSON.stringify(response.config.data);
        //当请求形式为get时要考虑请求的参数
        if (response.config.method == "get") {
          if (
            pending[p].u ===
              response.config.url + "&" + response.config.method &&
            pending.requstPrams === requstPrams
          ) {
            //当当前请求在数组中存在时执行函数体
            c(); //执行取消操作
            pending.splice(p, 1); //把这条记录从数组中移除
          }
        } else {
          if (
            pending[p].u ===
            response.config.url + "&" + response.config.method
          ) {
            c(); //执行取消操作
            pending.splice(p, 1); //把这条记录从数组中移除
          }
        }
      }
    });
    if (response.status === 200) {
      if (response.data.state && response.data.state != 0) {
        message.destroy();
        message.warning(response.data.msg);

        if (response.data.state === 4) {
          router.push("/login");
          sessionStorage.clear();
          setTimeout(() => {
            location.reload();
          }, 500);
        }
      }
      return Promise.resolve(response);
    } else {
      return Promise.reject(response);
    }
  },
  error => {
    try {
      if (error.response) {
        if (error.response.status === 500) {
          message.destroy();
          // message.error("请连接网络");
          return Promise.reject(error);
        } else if (error.status === 404 || error.response.status === 403) {
          router.push("/404");
        } else {
          message.destroy();
          // message.error("请连接网络");
          return Promise.reject(error);
        }
      } else {
        if (error.status === 500) {
          message.destroy();
          // message.error("请连接网络");
          return Promise.reject(error);
        } else if (error.status === 404 || error.status === 403) {
          router.push("/404");
          return Promise.reject(error);
        } else {
          message.destroy();
          // message.error("请连接网络");
          return Promise.reject(error);
        }
      }
    } catch (err) {
      message.error("请求超时");
    }
  }
);

export default instance;
